/* Container styling */
.container {
    background-color: #f0f8ff; /* Light background */
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Title styling */
  .title-text {
    color: #003366;
    font-family: 'Segoe UI', Tahoma, Geneva, sans-serif;
    font-size: 2.5rem;
    font-weight: bold;
    text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
  }
  
  /* Certification card styles */
  .certification-card {
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    background-color: #fff; /* White card background */
  }
  
  .certification-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  }
  
  .certification-card img {
    width: 100%;
    height: 220px;
    object-fit: cover;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
  }
  
  .certification-card .card-body {
    text-align: center;
    padding: 20px;
  }
  
  .certification-card .card-title {
    font-size: 1.4rem;
    font-weight: bold;
    color: #003366; /* Professional blue theme */
  }
  
  .certification-card .card-text {
    color: #333;
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .certification-card .btn {
    background-color: #00509e; /* Matching blue button */
    border: none;
    transition: background-color 0.3s ease;
  }
  
  .certification-card .btn:hover {
    background-color: #003366;
  }
  
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .certification-card img {
      height: 180px; /* Adjust height for smaller screens */
    }
    
    .certification-card .card-title {
      font-size: 1.2rem;
    }
  }
  