/* Container styling */
.container {
  background-color: #f0f8ff; /* Light background */
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

/* Title styling */
.title-text {
  color: #003366;
  font-family: 'Segoe UI', Tahoma, Geneva, sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
  text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

/* About card styles */
.about-card {
  border: none;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 30px;
  background-color: #fff; /* White card background */
}

/* About text styling */
.about-text {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 20px;
  line-height: 1.6;
}

/* Highlight section styling */
.highlight-section {
  background-color: #003366;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  margin-top: 20px;
}

.highlight-section h3 {
  color: #f0f8ff;
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.highlight-section ul {
  list-style: none;
  padding: 0;
}

.highlight-section li {
  font-size: 1.2rem;
  color: #f0f8ff;
  margin-bottom: 10px;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .title-text {
    font-size: 2rem;
  }

  .about-card {
    padding: 20px;
  }

  .about-text {
    font-size: 1rem;
  }
}
