/* Basic container styles */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background-color: #f0f8ff; /* Light white background to match About and Certification */
  height: 100vh;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

/* Profile photo styles */
.profile-photo {
  position: relative;
  padding: 10px;
  border-radius: 50%;
  background-color: #ffffff; /* Clear white background */
  box-shadow: 0 0 30px rgba(0, 75, 140, 0.2); /* Subtle shadow for clarity */
}

.photo {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  border: 5px solid #003366; /* Clear and sharp blue border */
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); /* Softer shadow for image clarity */
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

/* Hover effect for the photo */
.photo:hover {
  transform: scale(1.05);
  box-shadow: 0 0 30px rgba(0, 75, 140, 0.5);
}

/* Terminal window styles */
.terminal-window {
  width: 100%;
  max-width: 600px;
  background-color: #ffffff; /* White background to match About and Certification */
  color: #003366; /* Matching navy blue text */
  font-family: 'Segoe UI', Tahoma, Geneva, sans-serif;
  padding: 25px;
  border-radius: 12px;
  border: 2px solid rgba(0, 75, 140, 0.8); /* Blue border */
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.terminal-header {
  text-align: center;
  font-size: 1.6rem;
  margin-bottom: 15px;
  color: #003366; /* Navy blue header */
  letter-spacing: 1.5px;
}

.terminal-body {
  font-size: 1.2rem;
  line-height: 1.75;
  white-space: pre-wrap;
  letter-spacing: 1px;
  color: #003366; /* Matching deep blue text */
}

/* Typing cursor animation */
.cursor {
  display: inline-block;
  width: 12px;
  background-color: #003366;
  animation: blink 0.7s steps(1) infinite;
  margin-left: 5px;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .photo {
    width: 180px;
    height: 180px;
  }

  .terminal-window {
    width: 90%;
    padding: 20px;
  }

  .terminal-header {
    font-size: 1.4rem;
  }

  .terminal-body {
    font-size: 1rem;
  }
}
