/* Container styling */
.projects-container {
    background-color: #f0f8ff;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Title styling */
  .projects-title {
    color: #003366;
    font-family: 'Segoe UI', Tahoma, Geneva, sans-serif;
    font-size: 2.5rem;
    font-weight: bold;
    text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
  }
  
  /* Project card styling */
  .project-card {
    border: none;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    background-color: #fff;
  }
  
  .project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  }
  
  .project-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
  }
  
  .project-card .card-body {
    text-align: center;
    padding: 20px;
  }
  
  .project-card .card-title {
    font-size: 1.4rem;
    font-weight: bold;
    color: #003366;
    margin-bottom: 15px;
  }
  
  .project-card .card-text {
    color: #333;
    font-size: 1rem;
    margin-bottom: 20px;
    line-height: 1.6;
  }
  
  /* Button styling */
  .project-btn, .github-btn {
    margin: 10px 5px;
    display: inline-flex;
    align-items: center;
    padding: 10px 20px;
    font-size: 1.1rem;
    border-radius: 8px;
    text-decoration: none;
  }
  
  .project-btn {
    background-color: #00509e;
    color: #f0f8ff;
  }
  
  .github-btn {
    background-color: #333;
    color: #fff;
  }
  
  .project-btn .icon, .github-btn .icon {
    margin-right: 10px;
  }
  
  .project-btn:hover {
    background-color: #003366;
  }
  
  .github-btn:hover {
    background-color: #555;
  }
  
  /* Responsive styling */
  @media screen and (max-width: 768px) {
    .projects-title {
      font-size: 2rem;
    }
  
    .project-card .card-title {
      font-size: 1.2rem;
    }
  
    .project-card img {
      height: 150px;
    }
  
    .project-btn, .github-btn {
      font-size: 1rem;
      padding: 8px 15px;
    }
  }
  