/* Container styling */
.contact-container {
    background-color: #f0f8ff;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Title styling */
  .contact-title {
    color: #003366;
    font-family: 'Segoe UI', Tahoma, Geneva, sans-serif;
    font-size: 2.5rem;
    font-weight: bold;
    text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
  }
  
  /* Contact information styling */
  .contact-info {
    font-size: 1.2rem;
    color: #003366;
    line-height: 1.6;
  }
  
  /* Contact links (WhatsApp, Instagram, LinkedIn) */
  .contact-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .contact-link {
    color: #f0f8ff;
    background-color: #003366;
    padding: 10px 20px;
    border-radius: 8px;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    font-size: 1.1rem;
    transition: background-color 0.3s ease;
  }
  
  .contact-link .icon {
    margin-right: 10px;
  }
  
  .contact-link:hover {
    background-color: #00509e;
  }
  
  /* Email button styling */
  .email-btn {
    background-color: #00509e;
    border: none;
    padding: 10px 20px;
    font-size: 1.1rem;
    border-radius: 8px;
    display: inline-flex;
    align-items: center;
  }
  
  .email-btn .icon {
    margin-right: 10px;
  }
  
  .email-btn:hover {
    background-color: #003366;
  }
  
  /* Responsive styling */
  @media screen and (max-width: 768px) {
    .contact-title {
      font-size: 2rem;
    }
  
    .contact-links {
      flex-direction: column;
    }
  
    .contact-link {
      margin-bottom: 10px;
      width: 100%;
      text-align: center;
    }
  }
  