.experience-container {
    background-color: #f0f8ff;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  .card-title {
    font-size: 1.4rem;
    font-weight: bold;
    color: #003366;
  }
  
  .card-subtitle {
    font-size: 1.2rem;
    color: #00509e;
  }
  
  .card-text {
    color: #333;
    font-size: 1rem;
    line-height: 1.6;
  }
  
  /* Responsive styling */
  @media screen and (max-width: 768px) {
    .projects-title {
      font-size: 2rem;
    }
  
    .card-title {
      font-size: 1.2rem;
    }
  
    .card-img-top {
      height: 150px;
    }
  }
  